.features {
  min-width: 320px;
  height: 489px;
  background-size: 320px;
  background-position: 50% -15%;
  background-image: url("../img/hero-mobile.png");
  background-color: $features-background-color;
  background-repeat: no-repeat;
  margin-bottom: 29px;
  box-sizing: border-box;
  padding-top: 68px;
  border-top: 1px solid $outline-light-color;

  @media (min-width: $tablet-width) {
    height: 746px;
    background-position: 50% 50%;
    background-size: 768px;
    background-image: url("../img/hero-tablet.png");
    padding: 155px 50px 0 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    margin-bottom: 19px;

    &::after {
      content: "";
      position: absolute;
      bottom: -12px;
      left: 0;
      height: 24px;
      width: 100%;
      background-image: url("../img/zigzag-fill-tablet.svg");
    }
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    height: 748px;
    padding: 158px 50px 0 50px;
    background-position: 50% 50%;
    background-size: 1150px;
    background-image: url("../img/hero-desktop.png");
    margin-bottom: 70px;
    margin-right: auto;
    margin-left: auto;

    &::after {
      content: "";
      position: absolute;
      bottom: -17px;
      left: 0;
      height: 34px;
      width: 100%;
      background-image: url("../img/zigzag-fill-desktop.svg");
    }
  }
}

.features__title {
  display: block;
  width: 260px;
  height: 215px;
  margin: 0 auto;
  text-align: center;
  font-size: 27px;
  font-weight: bold;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
    width: 668px;
    height: auto;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
    width: 700px;
    height: auto;
  }
}
